.home {
  display: flex;
}
.home .homeContainer {
  flex: 6 1;
}
.home .homeContainer .widgets, .home .homeContainer .charts {
  display: flex;
  padding: 20px;
  grid-gap: 20px;
  gap: 20px;
}
.home .homeContainer .charts {
  padding: 5px 20px;
}
.home .homeContainer .listContainer {
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  padding: 20px;
  margin: 20px;
}
.home .homeContainer .listContainer .listTitle {
  font-weight: 500;
  color: gray;
  margin-bottom: 15px;
}
.widget {
  display: flex;
  justify-content: space-between;
  flex: 1 1;
  padding: 10px;
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  height: 100px;
}
.widget .left, .widget .right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.widget .left .title, .widget .right .title {
  font-weight: bold;
  font-size: 14px;
  color: #a0a0a0;
}
.widget .left .counter, .widget .right .counter {
  font-size: 28px;
  font-weight: 300;
}
.widget .left .link, .widget .right .link {
  width: -webkit-max-content;
  width: max-content;
  font-size: 12px;
  border-bottom: 1px solid gray;
}
.widget .left .percentage, .widget .right .percentage {
  display: flex;
  align-items: center;
  font-size: 14px;
}
.widget .left .percentage.positive, .widget .right .percentage.positive {
  color: green;
}
.widget .left .percentage.negative, .widget .right .percentage.negative {
  color: red;
}
.widget .left .icon, .widget .right .icon {
  font-size: 18px;
  padding: 5px;
  border-radius: 5px;
  align-self: flex-end;
}
